import { defineComponent } from "vue";
import AdminLayout from "@/layouts/Admin/index.vue";
import routes from "@/routes";

export default defineComponent({
  name: "Profile",
  components: {
    AdminLayout,
  },
  created() {
    if (!navigator.onLine) {
      routes.push({ name: "home.offline", params: { route: this.$route.fullPath } });
    }
  },
});
